import React from "react"
import "./styles.css"
import {useHistory} from "react-router-dom"

export default function LoginMsg(){
    var history = useHistory()
    function opLogin(){
        
        localStorage.setItem('path_return',window.location.pathname)
        history.push('/')
        
    }
    function opExit(){
        history.push('/home')
    }
    return (
        <div id="msg-login-root">
        <div class="msg-login">




        <div id="title-msg-login">
            Aviso
        </div>
        <div class="text-msg-login">
          Para acessar essas ferramentas  necessrio uma conta com permisses de administrador, caso tenha uma ja cadastrada no sistema, volte para tela de login, e entre com ela, caso no tenha uma, a nica maneira de conseguir  pedindo para que um administrador do sistema, ele avaliar se vc  adequo ao perfil necessrio:
          <ul>
            <li>Funcionrio do Restaurante;</li>
            <li>Manutencionista do <b>Sistema</b>;</li>
          </ul>
        </div>

        <div class="options-msg-login">
          <button onClick={() => opLogin()} type="button" name="button">Login</button>
          <button type="button" name="button">Solicitar conta de Administrador</button>
          <button onClick={() => opExit()} type="button" name="button">Voltar</button>
        </div>

    </div>
    </div>
    )
}